#footer {
    color: $site_footer;
    margin: 0 1rem;
    font-size: .7rem;
    color: $dimmed_text;
    clear: both;
    border-top: 1px solid $dimmed_text;
    max-width: 22rem;

    #altre-pagine {
        margin-top: 1rem;
        
        a {
            margin-right: .2rem;
        }
    }
    
    #impressum {
        margin-top: 1rem;
    }

    a {
        color: $site_footer;

        &:hover {
            color: $site_footer_hover_link;
        }
    }

    p {
        margin: 0;
    }
}

@media only screen and (max-width: 720px) {
    #footer {
        margin-left: 0;
        margin-right: 0;
    }
}
