#ritratto {
    float: right;
    margin: 0 0 2rem 2rem;
    width: 8rem;
    // box-shadow: 0 10px 10px 10px rgba(50, 50, 93, 0.25);
    display: block;

    svg {
        width: 100%;
        height: 100%;

        // #svg-sfondo { fill: #fff; }
        // #svg-4 { fill: #80e5ff; }
        // #svg-3 { fill: #454545; }
        // #svg-2 { fill: #f06; }
        // #svg-1 { fill: #fc0; }
        
        path {
            mix-blend-mode: multiply;
        }

        #svg-sfondo { fill: #fff; }
        #svg-4 { fill: #0ff; }
        #svg-3 { fill: #000; }
        #svg-2 { fill: #f0f; }
        #svg-1 { fill: #fc0; }
    }
}

@media only screen and (max-width: 500px) {
    #ritratto {
        float: none;
        margin: 2rem auto;
    }
}