html {
    font-size: $font_size;
}

body {
    background-color: $background_color;
    color: $text_color;
    font-family: $base_fonts, $emoji_fonts;
    line-height: 1.5;
    padding: 0 1rem;
}

a {
    color: $link;

    &:visited {
        color: $visited_link;
    }

    &:hover {
        color: $hover_link;
    }
}

h1, h2 {
    font-size: 1rem;
    font-weight: bold;
    margin-top: 2rem;
}

sup {
    line-height: 0;
    position: relative;
}

#chi-sono {
    margin: 3rem 0;
    font-size: 1.2rem;
    max-width: 34rem;

    em {
        text-decoration: underline;
        font-style: normal;
    }

    #suggerimento {
        font-size: 0.8rem;
        text-align: right;
        margin-top: 1rem;
        margin-right: -4rem;
        color: $dimmed_text;
    }
}

#content {
    margin: 0 3rem;

    #sections {
        margin: 2rem 0;

        h2 {
            font-weight: normal;
            margin-bottom: 0;
        }

        ul {
            margin-top: 0;
            list-style: none;
            padding-left: 0;

            .date {
                font-size: .8rem;
                vertical-align: middle;
                margin-left: 1rem;
            }
        }
    }
}


@media only screen and (max-width: 720px) {
    #chi-sono, #content {
        margin-left: 0;
        margin-right: 0;
    }

    #suggerimento {
        text-align: left !important;
        margin-right: 0;
    }
}