#header {
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    #logo {
        height: 2rem;

        svg {
            height: 100%;
            #nome {
               fill: #000000;
            }

            #background-splash {
                fill: #ffd500;
                opacity: 0;
                transition: .2s;
            }

            #riga {
                fill: #af0303;
                opacity: 0;
            }
        }

        &:hover svg #background-splash {
            opacity: 1;
        }

        &.home {
            svg #riga {
                opacity: 1;
            }
            
            &:hover svg #background-splash {
                    opacity: 0;
            }
        }
    }

    #pagine {
        font-weight: 300;
        text-transform: lowercase;
        
        a {
            margin-left: 1rem;
            text-decoration: none;
            font-size: .8rem;

            &:visited {
                color: $dimmed_text;
            }

            &:hover {
                text-decoration: underline;
            }
        }
    }
}

@media only screen and (max-width: 500px) {
    #header {
        flex-direction: column;
    }

    #pagine {
        a {
            margin-left: 0 !important;
            margin-right: 1rem;
        }
    }
}